body{background-color:#333336;font-family:"Roboto", sans-serif}#navbar{font-size:12px}.navbar-title-big{font-weight:bold;font-size:20px}.navbar-title{font-weight:bold;font-size:16px}.navbar-subtitle{font-weight:medium;font-size:12px}#navbar{position:relative;display:flex;justify-content:space-between;align-items:center;height:82px;width:80%;padding:0 10vw}#navbar #navbar-logo{height:50%;cursor:pointer}#navlinks-list{display:flex;justify-content:space-between;align-items:center;height:100%;width:50%}#navlinks-list .navlink-item{color:white;font-size:14px;height:100%;display:flex;justify-content:center;align-items:center;position:relative;padding:0 10px}#navlinks-list .navlink-item.selected::after,#navlinks-list .navlink-item.selectable:hover::after{content:"";position:absolute;bottom:-6px;z-index:20;border-right:20px solid transparent;border-left:20px solid transparent;border-bottom:16px solid white;margin:0 auto}.button{font-size:12px;padding:10px 25px;border-radius:30px;color:white;flex-shrink:0;margin:0 10px}#login{border:1px solid white}#login:hover{border:1px solid transparent;background:white;color:#6b6b6e}#signup{background:rgba(255,255,255,0.4)}#signup:hover{background:rgba(255,255,255,0.7)}.learn-more-btn{font-size:11px;color:#6b6b6e;background-color:#eeeef1;padding:5px 7px 5px 14px;border-radius:20px;display:flex;align-items:center}.learn-more-btn i{color:#666669;font-size:20px;vertical-align:middle;font-weight:bold}.learn-more-btn:hover{background-color:#e31b37;color:white}.learn-more-btn:hover i{color:white}.product-item{color:#8d8d90;display:flex;flex-direction:column;align-items:center;position:relative;margin:15px 0}.product-item::after{content:"";position:absolute;top:0;right:-30%;width:1px;height:300px;border-right:1px solid #f0f0f3}.product-item:first-of-type{margin-right:30%}.product-item:nth-last-of-type(2){margin-right:10%;margin-left:20%}.product-item:nth-last-of-type(3){margin-right:20%;margin-left:10%}.product-item:last-of-type{margin-left:30%}.product-item:last-of-type::after{display:none}.product-item>*{margin-bottom:15px}.product-item .product-image{width:78px}.product-item .product-description{font-size:12px;text-align:center;line-height:18px}.product-item .dropdown-link-list .product-link{margin-bottom:12px;color:#aaaaad;cursor:pointer}.product-item .dropdown-link-list .product-link:last-of-type{margin-bottom:0}.product-item .dropdown-link-list .product-link strong{color:#575759}.product-item .dropdown-link-list .product-link i{font-size:20px;vertical-align:middle}.industry-item{position:relative;top:0;display:flex;justify-content:center;align-items:center;width:100%;margin-right:8px;cursor:pointer}.industry-item .industry-image{min-width:220px;width:calc(100% - 8px);height:auto}.industry-item .industry-label{position:absolute;z-index:30;color:white;font-size:12px;line-height:14px;font-weight:lighter;text-transform:uppercase;text-align:center;letter-spacing:2.5px;max-width:calc(100vmax - 18px);padding:10px 20px;border-radius:20px;background-color:rgba(255,255,255,0.3)}.industry-item:first-of-type{margin-right:0}.industry-item:first-of-type .industry-image{position:relative;left:-6px;width:calc(100% - (8px * 1.05));transform:scale(1.05);transform-origin:top left}.industry-item:last-of-type{margin-right:0}.industry-item:hover{top:-3px}#dropdown-menu{position:absolute;top:82px;left:0;z-index:10;width:100%;background:white}#dropdown-menu #dropdown-menu-wrapper{width:80vmax;min-width:900px;padding:15px 0;margin:0 auto;display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:auto}#dropdown-menu #dropdown-menu-wrapper .dropdown-section-1{grid-column:1 / span 3}#dropdown-menu #dropdown-menu-wrapper .dropdown-section-2{grid-column:4 / span 3;align-items:center}#dropdown-menu #dropdown-menu-wrapper .dropdown-section-3{grid-column:7 / span 3;align-items:center}#dropdown-menu #dropdown-menu-wrapper .dropdown-section-4{grid-column:10 / span 3}
